.Overlay {
  position: relative;
}

.Notification {
  position: absolute;
  top: 0;
  right: 0;
  background-color: red;
  color: white;
  border-radius: 2px;
  font-size: 12px;
  padding: 7px 20px;
  margin: 0;
}

.Notification-fade-enter {
  transform: translateX(100%);
  opacity: 0;
}

.Notification-fade-enter-active {
  transform: translateX(0);
  transition: all 250ms linear;
  opacity: 1;
}

.Notification-fade-exit {
  transform: translateX(0);
  opacity: 1;
}

.Notification-fade-exit-active {
  transform: translateX(100%);
  transition: all 250ms linear;
  opacity: 0;
}
